import React, { useEffect, useState } from 'react';
import { Col, Container, Form, Image, Row, Button, ButtonGroup } from 'react-bootstrap';
import Moment from 'react-moment';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import cookies from 'react-cookies';
import httpRequest, { endpoints } from '~/configs/httpRequest';

function Home() {
    const customer = useSelector((state) => state.user.user);
    const [comments, setComments] = useState([]);
    const [commentContent, setCommentContent] = useState('');
    const [changed, setChanged] = useState(0);

    const [prev, setPrev] = useState(false);
    const [next, setNext] = useState(false);
    const [page, setPage] = useState(1);

    useEffect(() => {
        const loadComments = async () => {
            let query = `?page=${page}`;
            try {
                let res = await httpRequest.get(`${endpoints['comments']}${query}`);
                setComments(res.data.results);

                setNext(res.data.next !== null);
                setPrev(res.data.previous !== null);
            } catch (err) {
                console.error(err);
            }
        };
        loadComments();
    }, [changed, page]);

    const paging = (inc) => {
        setPage(page + inc);
    };

    const addComment = async (event) => {
        event.preventDefault();
        try {
            let res = await httpRequest.post(
                endpoints.addComment,
                {
                    content: commentContent,
                },
                {
                    headers: {
                        Authorization: `Bearer ${cookies.load('access_token')}`,
                    },
                },
            );

            console.info(res.data);
            comments.push(res.data);
            setComments(comments);
            setChanged(comments.length);
        } catch (err) {
            console.error(err);
        }
    };

    let comment = (
        <em>
            <Link to="/WeddingRestaurant/sign-in">ng nhp</Link>  bnh lun!
        </em>
    );

    if (customer !== null && customer !== undefined && customer.is_customer === true) {
        comment = (
            <Form onSubmit={addComment}>
                <Form.Group className="mb-3" controlId="commentContent">
                    <Form.Control
                        as="textarea"
                        placeholder="Nhp ni dung bnh lun..."
                        value={commentContent}
                        onChange={(event) => setCommentContent(event.target.value)}
                        rows={3}
                    />
                </Form.Group>
                <Button
                    type="submit"
                    variant="info"
                    style={{ marginBottom: '10px' }}
                    onClick={() => window.location.reload()}
                >
                    Thm bnh lun
                </Button>
            </Form>
        );
    }

    return (
        <Container>
            <Container
                className="text-center"
                style={{
                    margin: '40px auto',
                    fontFamily: "'Cormorant Garamond', serif",
                    fontSize: '18px',
                    fontStyle: 'italic',
                    color: '#888',
                }}
            >
                <h1>GII THIU CHUNG</h1>
                <p>
                    Trung tm T chc Tic ci c thnh lp t nm 2012 cung cp cho khch hng 05 dch v chnh, bao
                    gm: Dch v trang tr, Dch v Hi Ngh - Hi Tho, Dch v Tic Outside Catering, Dch v Tic
                    sinh nht v Dch v Wedding Planner.
                </p>
                <p>
                    S hu 12 c s ta lc ti cc v tr c a trong thnh ph H Ni v cc tnh ln cn, nh du
                    thng hiu vi phong cch Tr Trung - Thanh Lch - Hin i, Trng ng Palace t ho l im n
                    uy tn bc nht trong lnh vc t chc s kin v tic ci.
                </p>
                <p>
                    Chng ti lun ly khch hng lm trung tm v khng ngng n lc  mang n s hi lng cho khch
                    hng bng s chuyn nghip, hiu qu v tn tm. c gp phn vo s thnh cng trong s kin ca
                    bn chnh l nim hnh phc v vinh d ca chng ti!
                </p>
                <small>Ni gic m thnh hin thc!</small>
            </Container>
            <Container>{comment}</Container>
            <hr />
            <Container style={{ backgroundColor: 'hsla(204, 49%, 56%, 0.695)' }}>
                {comments.map((c, idx) => {
                    return (
                        <Row key={idx}>
                            <Col md={1} sm={4}>
                                <Image src={c.customer.user.avatar} roundedCircle fluid />
                            </Col>
                            <Col md={3} sm={4}>
                                <p>
                                    {c.customer.user.first_name} {c.customer.user.last_name}
                                </p>
                            </Col>
                            <Col md={8} xs={8}>
                                <p>{c.content}</p>
                                <p>
                                    Ngy bnh lun: {c.created_date.slice(0, 10)}
                                    {', lc: '}
                                    {c.created_date.slice(12, 19)}
                                </p>
                                <p>
                                    Cch y: <Moment fromNow>{c.created_date}</Moment>
                                </p>
                            </Col>
                        </Row>
                    );
                })}
                <ButtonGroup>
                    <Button variant="info" onClick={() => paging(-1)} disabled={!prev}>
                        &lt;&lt;
                    </Button>
                    <Button variant="info" onClick={() => paging(1)} disabled={!next}>
                        &gt;&gt;
                    </Button>
                </ButtonGroup>
            </Container>
        </Container>
    );
}

export default Home;
