import React from 'react';
import { Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import formatNumber from '../utils/formatNumber';

const HallCard = (props) => {
    let path = `/halls/${props.obj.id}`;
    return (
        <Card>
            <Link to={path} className="link-img">
                <Card.Img variant="top" src={props.obj.image} className="img-card" />
            </Link>
            <Card.Body>
                <Card.Title>{props.obj.name}</Card.Title>
                <Card.Text>Sc cha: {formatNumber(props.obj.capacity, 0, ',', '.')}</Card.Text>
                <Card.Text>Gi sng: {formatNumber(props.obj.morning_price, 0, ',', '.')}/vn</Card.Text>
                <Card.Text>Gi tra: {formatNumber(props.obj.noon_price, 0, ',', '.')}/vn</Card.Text>
                <Card.Text>Gi ti: {formatNumber(props.obj.night_price, 0, ',', '.')}/vn</Card.Text>
            </Card.Body>
            <Card.Footer>
                <b className={props.obj.free ? 'text-success' : 'text-danger'}>
                    {props.obj.free ? 'Cn trng' : ' c ngi t'}
                </b>
            </Card.Footer>
        </Card>
    );
};

export default HallCard;
